"use server";

import db from "../db";

export async function postReport(
  latitude: number,
  longitude: number,
  reportTypeId: number,
  trustScore: number,
  userId: string,
  description: string | null
  // image: string | null
) {
  try {
    const report = await db.report.create({
      data: {
        lat: latitude,
        long: longitude,
        description: description ?? null,
        //  image: image ?? null,
        reportTypeId,
        trustScore,
        submittedById: userId,
      },
    });

    await fetch("http://localhost:3005/api/new-report", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(report),
    });

    return report;
  } catch (error) {
    console.error(error);
  }
}
