"use client";

import { useState, useEffect } from "react";
import { postReport } from "@/actions/postReport";
import { getReportTypes } from "@/actions/getReportTypes";

interface PopupFormProps {
  latitude: number;
  longitude: number;
  userId: string;
  trustScore: number;
  onClose: () => void;
}

const PopupForm = ({
  latitude,
  longitude,
  userId,
  trustScore,
  onClose,
}: PopupFormProps) => {
  const [reportTypes, setReportTypes] = useState<ReportType[]>([]);
  const [selectedReportType, setSelectedReportType] = useState<number>(
    reportTypes.length > 0
      ? [...reportTypes].sort((a, b) => a.id - b.id)[0].id
      : 0
  );
  const [description, setDescription] = useState("");

  useEffect(() => {
    const fetchData = async () => {
      try {
        const types = await getReportTypes();
        setReportTypes(types);
      } catch (error) {
        console.error("Error fetching report types:", error);
      }
    };
    fetchData();
  }, []);

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (!selectedReportType) {
      return;
    }

    const payload = {
      latitude, // Current latitude
      longitude, // Current longitude
      reportTypeId: selectedReportType, // Admin can change these options
      trustScore,
      userId,
      description, // Optional description
      // Additional fields (like initial trustScore) can be set server-side.
    };

    postReport(
      latitude,
      longitude,
      selectedReportType,
      trustScore,
      userId,
      description
    );
    // Optionally clear the form if desired:
    setDescription("");
  };

  return (
    <div className="">
      <form onSubmit={handleSubmit} className="p-2">
        <div>
          <label htmlFor="reportType" className="block font-bold">
            Report Type:
          </label>
          <select
            id="reportType"
            value={selectedReportType}
            onChange={(e) => setSelectedReportType(Number(e.target.value))}
            required
            className="border rounded-sm p-1 w-full"
          >
            {reportTypes
              .slice()
              .sort((a, b) => a.id - b.id)
              .map((type) => (
                <option key={type.id} value={type.id}>
                  {type.name}
                </option>
              ))}
          </select>
        </div>
        <div className="mt-2">
          <label htmlFor="description" className="block font-bold">
            Description:
          </label>
          <textarea
            id="description"
            value={description}
            onChange={(e) => setDescription(e.target.value)}
            className="border rounded-sm p-1 w-full"
          />
        </div>
        <div className="mt-2">
          <p>
            <strong>Latitude:</strong> {latitude}
          </p>
          <p>
            <strong>Longitude:</strong> {longitude}
          </p>
        </div>
        <div className="mt-2 flex space-x-2">
          <button
            type="submit"
            className="bg-blue-500 text-white px-3 py-1 rounded-sm"
          >
            Submit Report
          </button>
          <button
            type="button"
            className="bg-gray-500 text-white px-3 py-1 rounded-sm"
            onClick={onClose}
          >
            Cancel
          </button>
        </div>
      </form>
    </div>
  );
};

export default PopupForm;
