"use client";

import { useState, useEffect, useRef } from "react";
import { Marker, Popup } from "react-map-gl/maplibre";
import PopupForm from "./PopupForm"; // Import the form for submitting
import { PositionAnchor } from "maplibre-gl";
import Image from "next/image";
import { set } from "better-auth";

export default function ReportSubmissionMarker({
  mapCenter,
  isAdding,
  setIsAdding,
}) {
  const [markerPosition, setMarkerPosition] = useState({
    longitude: mapCenter.longitude,
    latitude: mapCenter.latitude,
  });
  const [popupAnchor, setPopupAnchor] = useState<PositionAnchor>("bottom");
  const [popupOffset, setPopupOffset] = useState(60);

  const markerRef = useRef<HTMLDivElement | null>(null);

  useEffect(() => {
    setMarkerPosition({
      longitude: mapCenter.longitude,
      latitude: mapCenter.latitude,
    });
  }, [mapCenter]);

  useEffect(() => {
    updatePopupAnchor();
  }, [markerPosition]);

  const updatePopupAnchor = () => {
    if (!markerRef.current) {
      console.warn("‚ö†Ô∏è Marker ref is null!");
      return;
    }

    const markerRect = markerRef.current.getBoundingClientRect();
    const screenHeight = window.innerHeight;

    console.log(
      "üìè Marker position (Y):",
      markerRect.top,
      "Screen Height:",
      screenHeight
    );

    if (markerRect.top > (screenHeight / 5) * 2) {
      console.log("üìå Setting anchor: TOP");
      setPopupAnchor("bottom");
      setPopupOffset(60);
    } else {
      console.log("üìå Setting anchor: BOTTOM");
      setPopupAnchor("top");
      setPopupOffset(20);
    }
  };

  if (!isAdding) return null; // Don't show marker if not adding

  return (
    <>
      <Marker
        longitude={markerPosition.longitude}
        latitude={markerPosition.latitude}
        draggable
        anchor="bottom"
        style={{ zIndex: "10" }}
        onDragEnd={(e) => {
          const newCoords = { longitude: e.lngLat.lng, latitude: e.lngLat.lat };
          setMarkerPosition(newCoords);
          updatePopupAnchor();
        }}
        onDrag={(e) => {
          const newCoords = { longitude: e.lngLat.lng, latitude: e.lngLat.lat };
          setMarkerPosition(newCoords);
        }}
      >
        <Image
          src="/marker.png"
          width={35}
          height={35}
          alt="Marker"
          ref={markerRef}
        />
      </Marker>
      <Popup
        longitude={markerPosition.longitude}
        latitude={markerPosition.latitude}
        closeButton
        closeOnClick={false}
        onClose={() => setIsAdding(false)}
        anchor={popupAnchor}
        offset={popupOffset}
      >
        <PopupForm
          latitude={markerPosition.latitude}
          longitude={markerPosition.longitude}
          onClose={() => setIsAdding(false)}
        />
      </Popup>
    </>
  );
}
